import "./styles/Contact.css";
import React from 'react';
import takming1 from "./images/takming1.jpg";

export default function Contact() {
  return (
    <div className="contact-container" >
      <div className="image-text-layout" >
        
        <div>
          <img src={takming1} alt="" style={{ width: '100%', height: 'auto' }} />
        </div>

        <div className="text-container">
          <h3 className="contact-text"></h3>
          <h3 className="contact-text">1145156</h3>
          <h3 className="contact-text">aged101@takming.edu.tw</h3>
          <h3 className="contact-text">(02)26585801</h3>
          <h3 className="contact-text">  2401</h3>
        </div>
      </div>
    </div>
  );
}
