import React, { useState, useEffect } from "react";
import "./styles/taiwanese.css";
import AOS from "aos";
import taiwan from "./images/taiwan.png"; //
import Philippinessold from "./images/PhilippineLine.png"; //
import Philippines from "./images/Philippines.png"; //
import Taichung from "./images/Taichung.png"; // 
import Keelung from "./images/Keelung.png"; // 
import Taitung from "./images/Taitung.png"; //
import Tainan from "./images/Tainan.png"; //
import Yilan from "./images/Yilan.png"; //
import Hualien from "./images/Hualien.png"; //
import Nantou from "./images/Nantou.png"; //
import Pingtung from "./images/Pingtung.png"; //
import Kaohsiung from "./images/Kaohsiung.png"; //
import chiayi_city from "./images/chiayi city.png"; //
import Chiayi_County from "./images/Chiayi_County.png"; //
import Changhua from "./images/Changhua.png"; // 
import Yunlin from "./images/Yunlin.png"; //
import Miaoli from "./images/Miaoli.png"; //
import Hsinchu_City from "./images/Hsinchu_City.png"; //
import Hsinchu_County from "./images/Hsinchu_County.png"; //
import Taoyuan from "./images/Taoyuan.png"; //
import New_Taipei from "./images/New_Taipei.png"; //
import taipei from "./images/taipei.png"; //
import Philippinessever from "./images/philippinesevent.png"; //
import sever1 from "./images/nophoto.png"; //
import sever2 from "./images/TaipeiEventPhotos.png"; //
import sever3 from "./images/New TaipeiEventPhotos.png"; //
import Service from "../../../about_us/screens/Service"; //
import { BrowserRouter as Router, Route, Routes, Link } from "react-router-dom";

const TaiwanMap = () => {
  useEffect(() => {
    AOS.init({
      duration: 1000,
    });
  }, []);

  const defaultContent = "\n\n";
  const defaultImage = sever1;

  const [isDefault, setIsDefault] = useState(true);
  const [imgPath, setImgPath] = useState(taiwan);
  const [isPhilippines, setIsPhilippines] = useState(false);
  const [selectedContent, setSelectedContent] = useState(defaultContent);
  const [selectedImg, setSelectedImg] = useState(defaultImage);

  const handleClick = (newContent, newImg) => {
    setSelectedContent(newContent);
    setSelectedImg(newImg);
    setIsDefault(false);
  };

  const handleMouseOver = (newImgPath) => {
    setImgPath(newImgPath);
  };

  const handleMouseOut = () => {
    setImgPath(taiwan);
  };

  const handlePhilippinesOver = () => {
    setIsPhilippines(true);
  };

  const handlePhilippinesOut = () => {
    setIsPhilippines(false);
  };

  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  useEffect(() => {
    window.addEventListener("resize", () => {
      setWindowWidth(window.innerWidth);
    });
  }, []);
  return (
    <>
      {/* <div className="hr-line"></div> */}
      <h1
        data-aos="zoom-out-down"
        style={{
          textAlign: "center",
          color: "#fff",
          backgroundColor: "#90cbc0",
          width: "40%",
          marginRight: "30%",
          marginLeft: "30%",
          marginBottom: "20%",
        }}
      >
        
      </h1>

      {windowWidth > 800 ? (
        <div data-aos="fade-right"> 
        <div className="taiwan-map">
          <img src={imgPath} alt="123" style={{ width: "20%" }} />
          <button
            onMouseOver={() => handleMouseOver(Taichung)}
            onMouseOut={handleMouseOut}
            onClick={() =>
              handleClick("\n\n\n", sever1)
            }
            className="Taichung-button"
          >
            {/*  */}
          </button>

          <button
            onMouseOver={() => handleMouseOver(Keelung)}
            onMouseOut={handleMouseOut}
            onClick={() =>
              handleClick("\n\n\n", sever1)
            }
            className="Keelung-button"
          >
            {/*  */}
          </button>

          <button
            onMouseOver={() => handleMouseOver(Taitung)}
            onMouseOut={handleMouseOut}
            onClick={() =>
              handleClick("\n\n\n", sever1)
            }
            className="Taitung-button"
          >
            {/*  */}
          </button>

          <button
            onMouseOver={() => handleMouseOver(Tainan)}
            onMouseOut={handleMouseOut}
            onClick={() =>
              handleClick("\n\n\n", sever1)
            }
            className="Tainan-button"
          >
            {/*  */}
          </button>

          <button
            onMouseOver={() => handleMouseOver(Yilan)}
            onMouseOut={handleMouseOut}
            onClick={() =>
              handleClick("\n\n\n", sever1)
            }
            className="Yilan-button"
          >
            {/*  */}
          </button>

          <button
            onMouseOver={() => handleMouseOver(Hualien)}
            onMouseOut={handleMouseOut}
            onClick={() =>
              handleClick("\n\n\n", sever1)
            }
            className="Hualien-button"
          >
            {/*  */}
          </button>

          <button
            onMouseOver={() => handleMouseOver(Nantou)}
            onMouseOut={handleMouseOut}
            onClick={() =>
              handleClick("\n\n\n", sever1)
            }
            className="Nantou-button"
          >
            {/*  */}
          </button>

          <button
            onMouseOver={() => handleMouseOver(Pingtung)}
            onMouseOut={handleMouseOut}
            onClick={() =>
              handleClick("\n\n\n", sever1)
            }
            className="Pingtung-button"
          >
            {/*  */}
          </button>

          <button
            onMouseOver={() => handleMouseOver(Kaohsiung)}
            onMouseOut={handleMouseOut}
            onClick={() =>
              handleClick("\n\n\n", sever1)
            }
            className="Kaohsiung-button"
          >
            {/*  */}
          </button>

          <button
            onMouseOver={() => handleMouseOver(chiayi_city)}
            onMouseOut={handleMouseOut}
            onClick={() =>
              handleClick("\n\n\n", sever1)
            }
            className="chiayi_city-button"
          >
            {/*  */}
          </button>

          <button
            onMouseOver={() => handleMouseOver(Chiayi_County)}
            onMouseOut={handleMouseOut}
            onClick={() =>
              handleClick("\n\n\n", sever1)
            }
            className="Chiayi_County-button"
          >
            {/*  */}
          </button>

          <button
            onMouseOver={() => handleMouseOver(Changhua)}
            onMouseOut={handleMouseOut}
            onClick={() =>
              handleClick("\n\n\n", sever1)
            }
            className="Changhua-button"
          >
            {/*  */}
          </button>

          <button
            onMouseOver={() => handleMouseOver(Yunlin)}
            onMouseOut={handleMouseOut}
            onClick={() =>
              handleClick("\n\n\n", sever1)
            }
            className="Yunlin-button"
          >
            {/*  */}
          </button>

          <button
            onMouseOver={() => handleMouseOver(Miaoli)}
            onMouseOut={handleMouseOut}
            onClick={() =>
              handleClick("\n\n\n", sever1)
            }
            className="Miaoli-button"
          >
            {/*  */}
          </button>

          <button
            onMouseOver={() => handleMouseOver(Hsinchu_City)}
            onMouseOut={handleMouseOut}
            onClick={() =>
              handleClick("\n\n\n", sever1)
            }
            className="Hsinchu_City-button"
          >
            {/*  */}
          </button>

          <button
            onMouseOver={() => handleMouseOver(Hsinchu_County)}
            onMouseOut={handleMouseOut}
            onClick={() =>
              handleClick("\n\n\n", sever1)
            }
            className="Hsinchu_County-button"
          >
            {/*  */}
          </button>

          <button
            onMouseOver={() => handleMouseOver(Taoyuan)}
            onMouseOut={handleMouseOut}
            onClick={() =>
              handleClick("\n\n\n", sever1)
            }
            className="Taoyuan-button"
          >
            {/*  */}
          </button>

          <button
            onMouseOver={() => handleMouseOver(New_Taipei)}
            onMouseOut={handleMouseOut}
            onClick={() =>
              handleClick(
                "\n\n   109  10  11  109 .   109 (USR Hub)-.    130  11   109 .    ",
                sever3
              )
            }
            className="New_Taipei-button"
          >
            {/*  */}
          </button>

          <button
            onMouseOver={() => handleMouseOver(taipei)}
            onMouseOut={handleMouseOut}
            onClick={() =>
              handleClick(
                "\n\n  06  06   75        ",
                sever2
              )
            }
            className="taipei-button"
          >
            {/*  */}
          </button>

          <div className="Philippines-image">
            {isPhilippines ? (
              <img
                src={Philippines}
                alt="Philippines"
                style={{ width: "20%" }}
              />
            ) : (
              <img
                src={Philippinessold}
                alt="Philippines"
                style={{ width: "20%" }}
              />
            )}
            <button
              onMouseOver={handlePhilippinesOver}
              onMouseOut={handlePhilippinesOut}
              onClick={() =>
                handleClick(
                  "\n\n 100 ~110  11 ",
                  Philippinessever
                )
              }
              className="Philippines-button"
            >
              {/*  */}
            </button>
          </div>
          </div>
          <div className="content-box" > 
            <div
              style={{
                color: "#2a6663",
                fontSize: "16px",
                zIndex: 0.5,
                width: "35vw",
                textAlign: "justify",
                whiteSpace: "pre-wrap",
                marginTop: "15%",
                fontFamily: '"Noto Sans CJK TC", "Microsoft JhengHei", PingFang, STHeiti, sans-serif, serif',
              }}
            >
              {selectedContent}
            </div>

            {selectedImg && (
              <img
                src={selectedImg}
                alt="Selected"
                style={{
                  width: "39vw",
                  maxHeight: "30vh",
                  objectFit: "contain",
                  marginBottom: "2vh",
                  marginLeft: "-1vw",
                  marginTop: "5vh",
                }}
              />
            )}

            {!isDefault && (
              <Link
                to="/Service"
                target="_blank"
                style={{ textDecoration: "none", color: "inherit" }}
              >
                <button
                  className="twMin-more-button"
                  style={{
                    fontSize: "1vw", // 
                    padding: "1vh 0.5vw", // 
                    margin: "1vh 0", // 
                  }}
                  onClick={() => {
                    <Routes>
                      <Route path="/Service" element={<Service />} />
                    </Routes>;
                  }}
                >
                  
                </button>
              </Link>
            )}
          </div>
        </div>
      ) : (
        <>
          <div
            className="taiwan-map2"
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <img src={imgPath} alt="123" style={{ width: "30%" }} />
          </div>

          <div
            className="box"
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <div className="taiwan-map2" style={{ marginRight: "20px" }}>
              <img src={imgPath} alt="123" style={{ width: "30%" }} />
            </div>

            <Link
              to="/Service"
              target="_blank"
              style={{
                textDecoration: "none",
                color: "inherit",
                marginLeft: "500%",
              }}
            >
              <button
                className="twMax-more-button1"
                onClick={() => {
                  <Routes>
                    <Route path="/Service" element={<Service />} />
                  </Routes>;
                }}
              >
                
              </button>
            </Link>
          </div>
        </>
      )}
    </>
  );
};

export default TaiwanMap;
