import "../styles/ImportLink.css";
import React, { useEffect, useState } from "react";
import Contact from "../../home/components/contactus/Contact";
import AOS from "aos";
import { AiOutlineHome } from 'react-icons/ai';
import axios from "axios";


export default function Report() {
  const [Links, setLinks] = useState([]);

  useEffect(() => {
    AOS.init({
      duration: 700,
    });
    fetchLinks();
    // const mockLinks = [
    //   { name: 'Google', link: 'https://www.google.com' },
    //   { name: 'Facebook', link: 'https://www.facebook.com' },
    //   // ...
    // ];

    // setLinks(mockLinks);

  }, []);

  // const fileName = ".pdf";
  // const filePath = "/path/to/your/pdf/file.pdf";

  const fetchLinks = () => {
    axios.get("http://localhost:8080/Link/Linkall")
      .then(response => {
        console.log("", response.data);
        const oldLinks = [...Links];
        const updatedLinks = [...response.data, ...oldLinks];
        setLinks(updatedLinks);
      })
      .catch(error => {
        console.error("", error);
      });
  }

  return (
    <>
      <h1
        data-aos="zoom-out-down"
        style={{
          textAlign: "center",
          color: "#fff",
          backgroundColor: "#90cbc0",
          width: "40%",
          marginRight: "30%",
          marginLeft: "30%",
          marginTop: "13%",
        }}
      >
        
      </h1>
      <div className="centered-container-important">
        <div className="boxImport">
          {Links.map((Link, index) => (
            <React.Fragment key={index}>
              {Link.name}:<br />
              <a href={Link.link} target="_blank" rel="noreferrer">{Link.link}</a>
              <br />
            </React.Fragment>
          ))}

          <span> <a href="https://usr.moe.gov.tw/tw" target="_blank">https://usr.moe.gov.tw/tw</a></span>
          <br />
          <span>ESGUSR<a href="https://esg.gvm.com.tw/article/25878" target="_blank">https://esg.gvm.com.tw/article/25878</a></span>
          <br />
          <span>URS2023<a href="https://csr.cw.com.tw/article/43195" target="_blank">https://csr.cw.com.tw/article/43195</a></span>
          <br />
          <span>USR2022<a href="https://2022usrexpo.org/" target="_blank">https://2022usrexpo.org/</a></span>
          <br />
          <span>USR2021<a href="https://2021usrexpo.org/" target="_blank">https://2021usrexpo.org/</a></span>
          <br />
          <span>USR2020<a href="https://www.2020usrexpo.org/" target="_blank">https://www.2020usrexpo.org/</a></span>
          <br />
        </div>
      </div>

      {/* ======== */}
      <a href="http://localhost:3000/" className="return-But">
        <AiOutlineHome className="return-object"></AiOutlineHome>
      </a>
      <Contact />
    </>
  );
}
